<template>
  <card>
    <h4 class="card-title">성적 입력</h4>
    <div class="row">
      <div class="col-md-6">
        <base-input label="시험날짜(년)" >
        <select id="year" class="form-control" v-model="model.year">
                  <option >2015</option>
                  <option >2016</option>
                  <option >2017</option>
                  <option >2018</option>
                  <option >2019</option>
                  <option >2020</option>
                  <option >2021</option>
                  <option >2022</option>
                  <option >2023</option>
                  <option >2024</option>
                </select>
        </base-input>
      </div>
      <div class="col-md-6">
        <base-input label="시험날짜(월)" >
        <select id="month" class="form-control" v-model="model.month">
                  <option >01</option>
                  <option >02</option>
                  <option >03</option>
                  <option >04</option>
                  <option >05</option>
                  <option >06</option>
                  <option >07</option>
                  <option >08</option>
                  <option >09</option>
                  <option >10</option>
                  <option >11</option>
                  <option >12</option>
        </select>
        </base-input>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12">
        <base-input label="지각속도" v-model="model.score_1">
        </base-input>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12">
        <base-input label="공간능력" v-model="model.score_2">
        </base-input>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12">
        <base-input label="자료해석" v-model="model.score_3">
        </base-input>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12">
        <base-input label="언어논리" v-model="model.score_4">
        </base-input>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12">
        <base-input label="한국사" v-model="model.score_5">
        </base-input>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12">
        <base-button class="btn btn-md btn-block" type="primary" v-on:click="insert" fill>성적 등록</base-button>
      </div>
    </div>
  </card>
</template>
<script>
  import firebase from 'firebase';

  export default {
    props: {
      model: {
        type: Object,
        default: () => {
          return {};
        }
      }
    },
    methods: {
      insert () {          
        let email = firebase.auth().currentUser.email;
        if(this.model.year !== '' && this.model.month !== '' && this.model.score_1 !== '' && this.model.score_2 !== '' && this.model.score_3 !== '' && this.model.score_4 !== '' && this.model.score_5 !== ''){
            firebase.firestore().collection('scoreInfo').add({
            email: email,
            date: this.model.year + '/' + this.model.month,
            score_1: this.model.score_1,
            score_2: this.model.score_2,
            score_3: this.model.score_3,
            score_4: this.model.score_4,
            score_5: this.model.score_5
            }).then(() => {
            alert("성적 입력 완료");
            window.location.reload();
            }).catch( (err) => {
            alert("성적 입력 실패");
            })
        }
        else{
            alert('모든 정보가 입력되어야 합니다.');
        }        
      }
    },
    created (){
        this.model.year = new Date().getFullYear();
        this.model.month = new Date().getMonth() + 1;
        if(this.model.month < 10){
            this.model.month = '0' + this.model.month;
        }
    }
  }
</script>
<style>
</style>
