<template>
<div class="contact-us full-screen">
    <div class="wrapper wrapper-full-page section content">
      <div class="">
        <div class="container">
          <div class="row">
            <div class="col-md-8 col-md-offset-2 text-center">
                <card>        
                    <div class="register">
                        <base-input type="text" label="Email" placeholder="Email" v-model="model.email"></base-input>
                        <base-input type="password" label="Password" placeholder="Password" v-model="model.password"></base-input>
                        <base-input type="password" label="ConfirmPassword" placeholder="ConfirmPassword" v-model="model.confirmPassword"></base-input>
                        <base-input type="text" label="UserName" placeholder="UserName" v-model="model.userName"></base-input>
                        <base-button class="btn btn-md btn-block" slot="footer" type="primary" fill v-on:click="register">Register</base-button>
                        <p>Or go back to <router-link to="/login">login page</router-link></p>
                    </div>
                </card>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase';

export default {
  name: 'register',
  props: {
    model: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  methods: {
      register () {
        if (this.model.password === this.model.confirmPassword) {
          firebase.auth().createUserWithEmailAndPassword(this.model.email, this.model.password).then(
            (user) => {
              firebase.firestore().collection('userInfo').add({
                email: this.model.email,
                userName: this.model.userName
              })
              firebase.auth().signOut().then(() => {
                alert('계정이 생성되었습니다.')
                this.$router.replace('/login')
              })
            },
            (err) => {
              if (err.code === 'auth/email-already-in-use') {
                alert('이미 가입된 이메일 입니다.')
              } else if (err.code === 'auth/invalid-email') {
                alert('유효하지 않은 메일입니다')
              } else if (err.code === 'auth/operation-not-allowed') {
                alert('이메일 가입이 중지되었습니다.')
              } else if (err.code === 'auth/weak-password') {
                alert('비밀번호를 6자리 이상으로 설정해야합니다')
              } else {
                alert(err.message)
              }
            }
          )
        } else {
          alert('비밀번호가 일치하지 않습니다.')
        }
      }
  }
};
</script>

<style scoped>
.col-md-8{
  min-height: 90%;  /* Fallback for browsers do NOT support vh unit */
  min-height: 90vh; /* These two lines are counted as one :-)       */
  display: flex;
  align-items: center;
  margin: auto;
}
.register{
    text-align: left;
}

p{
    text-align: center;
}
</style>
