<template>
<div class="contact-us full-screen">
    <div class="wrapper wrapper-full-page section content">
      <div class="">
        <div class="container">
          <div class="row">
            <div class="col-md-8 col-md-offset-2 text-center">
                <card>           
                <div class="login">
                    <base-input class="login-form" type="text" label="Email" placeholder="Email" v-model="model.email"></base-input>
                    <base-input class="login-form" type="password" label="Password" placeholder="Password" v-model="model.password"></base-input>        
                    <base-button class="btn btn-md btn-block" slot="footer" type="primary" fill v-on:click="login">Login</base-button>
                    <p>You don't have an account? <router-link to="/register">create one!</router-link></p>
                </div>
                </card>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase';

export default {
  name: 'login',
  props: {
    model: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  methods: {
      login () {        
        firebase.auth().signInWithEmailAndPassword(this.model.email, this.model.password).then(
          (user) => {
            this.$router.replace('/dashboard')
          },
          (err) => {
            alert('Oops. ' + err.message)
          }
        )
      }
  }
};
</script>

<style scoped>
.col-md-8{
  min-height: 90%;  /* Fallback for browsers do NOT support vh unit */
  min-height: 90vh; /* These two lines are counted as one :-)       */
  display: flex;
  align-items: center;
  margin: auto;
}
.login{
    text-align: left;
}
p{
    text-align: center;
}
.login-form{
    text-align: left;
}
</style>
