<template>
  <card>
    <h4 class="card-title">개인 정보</h4>
    <div class="row">
      <div class="col-md-12">
        <base-input label="email" v-model="model.email" disabled>
        </base-input>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12">
        <base-input label="이름" v-model="model.name"></base-input>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12">
        <base-input type="password" label="현재 비밀번호" v-model="model.password"></base-input>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12">
        <base-input type="password" label="새 비밀번호" v-model="model.newPassword"></base-input>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12">
        <base-input type="password" label="새 비밀번호 확인" v-model="model.confirmNewPassword"></base-input>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12">
        <base-button class="btn btn-md btn-block" type="primary" v-on:click="editProfile" fill>정보 수정</base-button>
      </div>
    </div>
  </card>
</template>
<script>
  import firebase from 'firebase';

  export default {
    props: {
      model: {
        type: Object,
        default: () => {
          return {};
        }
      }
    },
    methods: {
      editProfile () {
        firebase.firestore().collection('userInfo').where('email', '==', this.model.email).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            firebase.firestore().collection('userInfo').doc(doc.id).update({userName: this.model.name});
          })
        }).then(() => {
          alert("정보 수정 완료");
          this.$router.replace('/profile');
        }).catch( (err) => {
          alert("정보 수정 실패");
        })
      }
    }
  }
</script>
