<template>
  <div>
    <modal :show.sync="modalVisible"
               body-classes="p-0"
               :centered="false"
               class="test"
               id="modal">               
            <card type="secondary" header-classes="bg-white pb-4" body-classes="px-lg-4 py-lg-4" class="border-0 mb-0" >
                <template>
                    <button type="button" data-dismiss="modal" aria-label="Close" class="close" v-on:click="closeModal"><i class="tim-icons icon-simple-remove"></i></button>
                    <div class="row">
                      <div class="col-md-6">
                        <base-input label="시험일자(년)" >
                        <select id="year" class="form-control" v-model="editFormData.year">
                                  <option >2015</option>
                                  <option >2016</option>
                                  <option >2017</option>
                                  <option >2018</option>
                                  <option >2019</option>
                                  <option >2020</option>
                                  <option >2021</option>
                                  <option >2022</option>
                                  <option >2023</option>
                                  <option >2024</option>
                                </select>
                        </base-input>
                      </div>
                      <div class="col-md-6">
                        <base-input label="시험일자(월)" >
                        <select id="month" class="form-control" v-model="editFormData.month">
                                  <option >01</option>
                                  <option >02</option>
                                  <option >03</option>
                                  <option >04</option>
                                  <option >05</option>
                                  <option >06</option>
                                  <option >07</option>
                                  <option >08</option>
                                  <option >09</option>
                                  <option >10</option>
                                  <option >11</option>
                                  <option >12</option>
                        </select>
                        </base-input>
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-md-12">
                        <base-input class="login-form" type="textarea" label="지각속도" v-model="editFormData.score_1">
                        </base-input>
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-md-12">
                        <base-input label="공간능력" v-model="editFormData.score_2">
                        </base-input>
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-md-12">
                        <base-input label="자료해석" v-model="editFormData.score_3">
                        </base-input>
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-md-12">
                        <base-input label="언어논리" v-model="editFormData.score_4">
                        </base-input>
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-md-12">
                        <base-input label="한국사" v-model="editFormData.score_5">
                        </base-input>
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-md-12">
                        <base-button class="btn btn-md btn-block" type="primary" v-on:click="editScore" fill>성적 수정</base-button>
                      </div>
                    </div>
                </template>
            </card>
        </modal>

    <div class="row">
      <div class="col-12">
        <card type="chart">
          <template slot="header">
            <div class="row">
              <div class="col-sm-6" :class="isRTL ? 'text-right' : 'text-left'">
                <h4 class="card-title">모의고사 점수</h4>
              </div>
              <div class="col-sm-6">
                <div class="btn-group btn-group-toggle"
                     :class="isRTL ? 'float-left' : 'float-right'"
                     data-toggle="buttons">
                  <label v-for="(option, index) in bigLineChartCategories"
                         :key="option"
                         class="btn btn-sm btn-primary btn-simple"
                         :class="{active: bigLineChart.activeIndex === index}"
                         :id="index">
                    <input type="radio"
                           @click="initBigChart(index)"
                           name="options" autocomplete="off"
                           :checked="bigLineChart.activeIndex === index">
                    {{option}}
                  </label>
                </div>
              </div>
            </div>
          </template>
          <div class="chart-area">
            <line-chart style="height: 100%"
                        ref="bigChart"
                        chart-id="big-line-chart"
                        :chart-data="bigLineChart.chartData"
                        :gradient-colors="bigLineChart.gradientColors"
                        :gradient-stops="bigLineChart.gradientStops"
                        :extra-options="bigLineChart.extraOptions">
            </line-chart>
          </div>
        </card>
      </div>
    </div>
    <div class="row">
      <div class="col-12">
        <card :title="table1.title">
          <div class="table-responsive">
            <score-table :data="table1.data"
                        :columns="table1.columns"
                        thead-classes="text-primary"
                         v-on:editScore="showModal">
            </score-table>
          </div>
        </card>
      </div>
    </div>
  </div>
</template>
<script>
  import LineChart from '@/components/Charts/LineChart';
  import * as chartConfigs from '@/components/Charts/config';
  import ScoreTable from './Score/ScoreTable';
  import config from '@/config';
  import firebase from 'firebase';
  import Modal from '@/components/Modal';

  const tableColumns = ["시험일자", "지각속도", "공간능력", "자료해석", "언어논리", "한국사", "총점"];

  export default {
    components: {
      LineChart,
      ScoreTable,
      Modal
    },
    data() {
      return {
        bigLineChart: {
          allData:  [ [],[],[],[],[] ],
          activeIndex: 0,
          chartData: null,
          extraOptions: chartConfigs.purpleChartOptions,
          gradientColors: config.colors.primaryGradient,
          gradientStops: [1, 0.4, 0],
          categories: [],
          labels: [],
        },
        table1: {
          title: "모의고사 점수",
          columns: [...tableColumns],
          data: []
        },
        chart: {
          label: [],
          data: [ [], [], [], [], [], [] ],
          ydata: [ [], [], [], [], [], [] ],
          ydata_total: [ [], [], [], [], [], [] ]
        },
        modalVisible: false,
        editFormData:{
          year: '',
          month: '',
          score_1: '',
          score_2: '',
          score_3: '',
          score_4: '',
          score_5: '',
          id: ''
        }
      }
    },
    computed: {
      enableRTL() {
        return this.$route.query.enableRTL;
      },
      isRTL() {
        return this.$rtl.isRTL;
      },
      bigLineChartCategories() {
        return this.$t('dashboard.chartCategories');
      }
    },
    methods: {
      initBigChart(index) {
        let chartData = {
          datasets: [{
            fill: true,
            borderColor: config.colors.primary,
            borderWidth: 2,
            borderDash: [],
            borderDashOffset: 0.0,
            pointBackgroundColor: config.colors.primary,
            pointBorderColor: 'rgba(255,255,255,0)',
            pointHoverBackgroundColor: config.colors.primary,
            pointBorderWidth: 20,
            pointHoverRadius: 4,
            pointHoverBorderWidth: 15,
            pointRadius: 4,
            data: this.bigLineChart.allData[index]
          },
           {
            type: 'line',
            data: index == 5 ? this.chart.ydata[0] : null,
            backgroundColor: 'rgba(255, 68, 0, 0.4)',
            borderColor: 'rgba(255, 68, 0, 1)'
          }],
          labels: this.bigLineChart.labels,
        }
        this.$refs.bigChart.updateGradients(chartData);
        this.bigLineChart.chartData = chartData;
        this.bigLineChart.activeIndex = index;
      },
      showModal(editFormData) {     
        console.log(editFormData);   
        this.editFormData.year    = editFormData.year   
        this.editFormData.month   = editFormData.month  
        this.editFormData.score_1 = editFormData.score_1
        this.editFormData.score_2 = editFormData.score_2
        this.editFormData.score_3 = editFormData.score_3
        this.editFormData.score_4 = editFormData.score_4
        this.editFormData.score_5 = editFormData.score_5
        this.editFormData.id      = editFormData.id
        this.modalVisible = true;
      },
      closeModal(){
        this.modalVisible = false;
      },
      editScore() {
           
        let id = this.editFormData.id;
        firebase.firestore().collection('scoreInfo').doc(id).update({          
            date: this.editFormData.year + '/' + this.editFormData.month,
            score_1: this.editFormData.score_1,
            score_2: this.editFormData.score_2,
            score_3: this.editFormData.score_3,
            score_4: this.editFormData.score_4,
            score_5: this.editFormData.score_5
            }).then(() => {
                alert("성적 수정 완료");
                this.modalVisible = false;
                window.location.reload();
                }).catch( (err) => {
                  console.log(err)
                alert("성적 수정 실패");
                });
      }
    },
    mounted() {
      let email = firebase.auth().currentUser.email;
        firebase.firestore().collection('scoreInfo').where('email', '==', email).orderBy('date', 'desc').get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              this.table1.data.push({
                    시험일자 : doc.data().date,
                    지각속도 : doc.data().score_1,
                    공간능력 : doc.data().score_2,
                    자료해석 : doc.data().score_3,
                    언어논리 : doc.data().score_4,
                    한국사   : doc.data().score_5,
                    총점     : parseInt(doc.data().score_1) + parseInt(doc.data().score_2) + parseInt(doc.data().score_3) + parseInt(doc.data().score_4) + parseInt(doc.data().score_5),
                    docId   : doc.id,
              })
              this.chart.label.push(doc.data().date);
              this.chart.data[0].push(doc.data().score_1);
              this.chart.data[1].push(doc.data().score_2);
              this.chart.data[2].push(doc.data().score_3);
              this.chart.data[3].push(doc.data().score_4);
              this.chart.data[4].push(doc.data().score_5);
              this.chart.data[5].push(parseInt(doc.data().score_1) + parseInt(doc.data().score_2) + parseInt(doc.data().score_3) + parseInt(doc.data().score_4) + parseInt(doc.data().score_5));
              this.chart.ydata[0].push(12);
            })
            this.bigLineChart.labels = this.chart.label.reverse();
            this.bigLineChart.allData[0] = this.chart.data[0].reverse();
            this.bigLineChart.allData[1] = this.chart.data[1].reverse();
            this.bigLineChart.allData[2] = this.chart.data[2].reverse();
            this.bigLineChart.allData[3] = this.chart.data[3].reverse();
            this.bigLineChart.allData[4] = this.chart.data[4].reverse();
            this.bigLineChart.allData[5] = this.chart.data[5].reverse();
      }).then(() => {
        this.initBigChart(5);
      })

      this.i18n = this.$i18n;
      if (this.enableRTL) {
        this.i18n.locale = 'ar';
        this.$rtl.enableRTL();
      }
    },
    beforeDestroy() {
      if (this.$rtl.isRTL) {
        this.i18n.locale = 'en';
        this.$rtl.disableRTL();
      }
    }
  };

</script>
<style scoped>
.icon-simple-remove::before{
  color: white;
}
.test{
  top: -10%;
}
.table-responsive{
    overflow:hidden;
}
</style>