<template>
  <div class="row">
    <div class="col-md-12">
      <edit-profile-form :model="model">
      </edit-profile-form>
    </div>
  </div>
</template>
<script>
  import EditProfileForm from './Profile/EditProfileForm';
  import firebase from 'firebase';
  export default {
    components: {
      EditProfileForm
    },
    data() {
      return {
        model: {
          email: '',
          name: '',
          password: '',
          newPassword: '',
          confirmNewPassword: '',
        }
      }
    },
    created () {
      let email = firebase.auth().currentUser.email;
      firebase.firestore().collection('userInfo').where('email', '==', email).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.model.email = doc.data().email;
            this.model.name = doc.data().userName;
            console.log(doc.id, ' => ', doc.data())
          })
    })
    }
  }
</script>
