<template>
<div>
  <table class="table tablesorter" :class="tableClass">
    <thead :class="theadClasses">
    <tr>
      <slot name="columns">
        <th v-for="column in columns" :key="column">{{column}}</th>
        <th>수정 / 삭제</th>
      </slot>
    </tr>
    </thead>
    <tbody :class="tbodyClasses">
    <tr v-for="(item, index) in data" :key="index">
      <slot :row="item">
        <td v-for="(column, index) in columns"
            :key="index"
            v-if="hasValue(item, column)">
          {{itemValue(item, column)}}
        </td>
        <td class="td-actions">
          <base-button type="link" aria-label="edit button" v-on:click="showModal(index)">
            <i class="tim-icons icon-pencil"></i>
          </base-button>
          <base-button type="link" aria-label="edit button" v-on:click="deleteScore(index)">
            <i class="tim-icons icon-trash-simple"></i>
          </base-button>
        </td>
      </slot>
    </tr>
        
    </tbody>
  </table>
</div>
</template>
<script>
  import firebase from 'firebase';
  import Modal from '@/components/Modal';

  export default {
    components: {
      Modal
    },
    name: 'score-table',
    data () {
      return {
        modalVisible: false,
        searchModalVisible: false,
        editFormData:{
          year: '',
          month: '',
          score_1: '',
          score_2: '',
          score_3: '',
          score_4: '',
          score_5: '',
          id: ''
        }
        }
    },
    props: {      
      columns: {
        type: Array,
        default: () => [],
        description: "Table columns"
      },
      data: {
        type: Array,
        default: () => [],
        description: "Table data"
      },
      type: {
        type: String, // striped | hover
        default: "",
        description: "Whether table is striped or hover type"
      },
      theadClasses: {
        type: String,
        default: '',
        description: "<thead> css classes"
      },
      tbodyClasses: {
        type: String,
        default: '',
        description: "<tbody> css classes"
      },
    },
    computed: {
      tableClass() {
        return this.type && `table-${this.type}`;
      }
    },
    methods: {
      hasValue(item, column) {
        return item[column.toLowerCase()] !== "undefined";
      },
      itemValue(item, column) {
        return item[column.toLowerCase()];
      },
      editScore(index) {        
        let id = this.editFormData.id;
        firebase.firestore().collection('scoreInfo').doc(id).update({          
            date: this.editFormData.year + '/' + this.editFormData.month,
            score_1: this.editFormData.score_1,
            score_2: this.editFormData.score_2,
            score_3: this.editFormData.score_3,
            score_4: this.editFormData.score_4,
            score_5: this.editFormData.score_5
            }).then(() => {
                alert("성적 수정 완료");
                this.modalVisible = false;
                window.location.reload();
                }).catch( (err) => {
                  console.log(err)
                alert("성적 수정 실패");
                });
      },
      deleteScore(index) {
        if(confirm('삭제 하시겠습니까?')){
          let id = this.data[index].docId;
          firebase.firestore().collection('scoreInfo').doc(id).delete().then(() => {
              alert("성적 삭제 완료");
              window.location.reload();
              }).catch( (err) => {
              alert("성적 삭제 실패");
              });
          };        
      },
      showModal(index) {
        this.editFormData.year = this.data[index].시험일자.split('/')[0]
        this.editFormData.month = this.data[index].시험일자.split('/')[1]
        this.editFormData.score_1 = this.data[index].지각속도
        this.editFormData.score_2 = this.data[index].공간능력
        this.editFormData.score_3 = this.data[index].자료해석
        this.editFormData.score_4 = this.data[index].언어논리
        this.editFormData.score_5 = this.data[index].한국사
        this.editFormData.id = this.data[index].docId
        this.$emit('editScore', this.editFormData);
        this.modalVisible = true;
      },
      closeModal(){
        this.modalVisible = false;
      }
    }
  };
</script>
